.sensitive-button {
  margin: 50px 50% 70px 50%;
  border:1px solid #00abea;
  border-radius:5px;
  width:108px;
  height:38px;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  background-color: white;
  color: #00ABEA;
  font-weight: bold;
}

.sensitive-default {
  margin: 50px 50% 70px 50%;
  border:1px solid #a4aab3;
  border-radius:5px;
  width:108px;
  height:38px;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  background-color: white;
  color: #A4AAB3;
  font-weight: bold;
}

.sensitive-font{
  font-family:PingFangSC-Regular;
  font-size:10px;
  color:#4a4a4a;
  margin-right: 15px;
}

.sensitive-a{
  float: right;
}

.sensitive-a a{
  margin-left: 15px;
}

.risk-font{
  margin-left: 20px;
  font-family:PingFangSC-Medium;
  font-size:18px;
  color:#4a4a4a;
}

.risk-close{
  background-image: url("../../images/sensitive/close.png");
  background-size:15px;
  float: right;
  width: 15px;
  height:15px;
  margin: 5px 15px 0 0;
}
.risk-title{
  margin-top: 25px;
}
.risk-table{
  margin: 15px;
}
